  padding: 130px 15px 0;
  margin: 0 auto;
  height: 100vh;
  overflow: hidden;
}

@media screen and (max-width: 768px) {
  .container {
    flex-wrap: wrap;
    justify-content: center;
  }
}

.container h1 {
  margin-top: 120px;
  color: #E30E18;
  font-size: 3.55rem;
  font-weight: normal;
  text-transform: uppercase;
}

@media screen and (max-width: 768px) {
  .container h1 {
    margin-top: 50px;
    background-color: #fff;
  }
}

.logo {
  display: block;
  margin-top: 46px;
}

.button {
  display: inline-block;
  margin-top: 60px;
  border-radius: 10px;
  transition: all 0.25s;
  max-width: 45%;
}

.button:last-child {
  margin-left: 43px;
}

@media screen and (max-width: 768px) {
  .button:last-child {
    margin-left: 10px;
  }
}

.button:hover {
  box-shadow: 5px 5px 40px 3px rgba(154, 154, 154, 0.3);
}

.phone {
  margin-left: 150px;
  margin-top: 0;
}

@media screen and (max-width: 768px) {
  .phone {
    margin-left: 0;
  }
}

@media screen and (max-width: 420px) {
  .phone {
    margin-top: -70px;
  }
}

.button-wrapper {
  display: block;
}

.section-heading {
  font-size: 36px;
  font-weight: bold;
  text-align: center;
  line-height: 61px;
  text-transform: capitalize;
}

@media screen and (min-width: 1025px) {
  .section-heading {
    font-size: 48px;
  }
}

.section-caption {
  font-size: 14px;
  font-weight: 300;
  line-height: 2;
  text-align: center;
  color: #808080;
  max-width: 560px;
  margin: 25px auto 0;
  letter-spacing: 0.24px;
}

@media screen and (min-width: 768px) {
  .section-caption {
    margin-top: 50px;
  }
}

.light-text {
  font-size: 36px;
  font-weight: 300;
  line-height: 47px;
