.section-heading {
  font-size: 36px;
  font-weight: bold;
  text-align: center;
  line-height: 61px;
  text-transform: capitalize;

  @media screen and (min-width: $medium-screen){
    font-size: 48px;
  }
}

.section-caption {
  font-size: 14px;
  font-weight: 300;
  line-height: 2;
  text-align: center;
  color: $grey;
  max-width: 560px;
  margin: 25px auto 0;
  letter-spacing: 0.24px;

  @media screen and (min-width: $tablet-screen) {
    margin-top: 50px;
  }
}

.light-text {
  font-size: 36px;
  font-weight: 300;
  line-height: 47px;
  display: block;
  color: $forest-green;

  @media screen and (min-width: $medium-screen){
    font-size: 48px;
    line-height: 61px;
  }
}

.text {
  font-weight: 300;
  font-size: 14px;
  color: #000;
  line-height: 1.8;
}